	module.exports = {"data":{"markdownRemark":{"html":"<h2>How to Use SFTP to Securely Transfer Files with a Remote Server</h2>\n<p>This article is a quick tutorial on how to use Secure File Transfer Protocol (SFTP) to exchange files with a server. This is useful for programming, as it allows you to code and test locally, and then send your work to the server when you are done.</p>\n<h3>Testing SSH</h3>\n<p>If you haven’t already, test that you are able to SSH into the server. SFTP uses the Secure Shell (SSH) protocol, so if you are unable to SSH you probably won’t be able to SFTP either.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>ssh your_username@hostname_or_ip_address</code></pre>\n      </div>\n<h3>Start SFTP Session</h3>\n<p>This uses the same syntax as SSH and opens a session in which you can transfer files.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>sftp your_username@hostname_or_ip_address</code></pre>\n      </div>\n<p>To list helpful commands:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>help</code></pre>\n      </div>\n<h3>Transfer files and folders</h3>\n<p>To download a file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>get <filename></code></pre>\n      </div>\n<p>To download a folder and its contents, use the “-r” flag (also works for uploading):</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>get -r <foldername></code></pre>\n      </div>\n<p>To upload a file:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>put <filename></code></pre>\n      </div>\n<h3>Change folders</h3>\n<p>To change the local folder:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>lcd <path/to/folder></code></pre>\n      </div>\n<p>To change the remote folder:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-unix\"><code>cd <path/to/folder></code></pre>\n      </div>","fields":{"slug":"/linux/how-to-use-sftp-to-securely-transfer-files-with-a-remote-server/"},"frontmatter":{"title":"How to Use SFTP to Securely Transfer Files with a Remote Server"}}},"pathContext":{"slug":"/linux/how-to-use-sftp-to-securely-transfer-files-with-a-remote-server/"}}

